.body {
  letter-spacing: 0;
  padding: 0;
  font-family: Quicksand, serif;
  font-style: normal;
  font-weight: 400;
}

.search-form {
  justify-content: center;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-right: 2px;
  transition: box-shadow 1s cubic-bezier(.43, .41, .22, .91);
  box-shadow: inset 0 2px 15px rgba(0, 0, 0, .75);
}

.container {
  background-color: #000308;
  margin: 0;
}

button {
  border-radius: 5px;
}

.gallery {
  background-color: #e2e0dc;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info {
  background: linear-gradient(to bottom left, #010000 40%, #ffc39e 100%);
  justify-content: center;
  display: flex;
}

.photo-card {
  width: 320px;
  margin: 5px;
  transition: box-shadow .5s cubic-bezier(.43, .41, .22, .91);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, .25);
}

.info-item {
  color: #fff;
  letter-spacing: .2px;
  flex-direction: column;
  margin-right: 15px;
  display: flex;
}

.info-item:last-child {
  margin-right: 0;
}

.container-btn {
  justify-content: center;
  align-items: center;
  display: flex;
}

.button.is-hidden, button > .spinner.is-hidden {
  display: none;
}

.label {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn-light {
  transition: box-shadow 1s cubic-bezier(.43, .41, .22, .91);
  box-shadow: inset 0 2px 15px rgba(0, 0, 0, .75);
}

.btn-light:hover, .btn-light:focus {
  --bs-btn-focus-shadow-rgb: 0;
}

.btn:hover, .btn:focus {
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, .25);
}

